<template>
  <div class="detail">
    <div class="detail-swipe" @click="showControl">
      <van-swipe
        class="detail-swipe"
        @change="onChange"
        :show-indicators="false"
        :initial-swipe="current"
      >
        <van-swipe-item class="detail-swipe" v-for="(imageSrc,index) in images" :key="index">
          <!-- <lazy-component class="image"> -->
          <van-image class="image" :src="imageSrc" fit="contain" />
          <!-- </lazy-component> -->
        </van-swipe-item>
      </van-swipe>
    </div>
    <div class="header" v-show="showControlView">
      <van-icon name="cross" size="24" @click="goBack" />
      <span>{{ current + 1 }}/{{images.length}}</span>
    </div>

    <van-tabs
      v-show="showControlView"
      class="tabs-image"
      @change="onChange"
      v-model="current"
      scrollspy
      sticky
    >
      <van-tab v-for="(imageSrc,index) in images" :key="index">
        <template #title>
          <lazy-component class="thumb-wrap">
            <van-image class="thumb" :src="imageSrc" fit="fill" />
          </lazy-component>
        </template>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import { Tabs, Tab, Icon, Swipe, SwipeItem, Image, Toast } from "vant";

export default {
  components: {
    [Tabs.name]: Tabs,
    [Tab.name]: Tab,
    [Icon.name]: Icon,
    [Swipe.name]: Swipe,
    [SwipeItem.name]: SwipeItem,
    [Image.name]: Image
  },

  data() {
    return {
      showControlView: true,
      current: 0,
      images: []
    };
  },
  created() {
    let article_detail = null;
    try {
      article_detail = JSON.parse(localStorage.getItem("article_detail"));
    } catch (error) {}
    if (!article_detail) {
      article_detail = window._article_detail;
    }
    if (
      article_detail &&
      article_detail.images &&
      article_detail.images.length > 0
    ) {
      this.images = article_detail.images;
    } else {
      this.$router.replace("main");
    }
  },
  methods: {
    onChange(index) {
      this.current = index;
    },
    showControl() {
      this.showControlView = !this.showControlView;
    },
    goBack() {
      this.$router.goBack();
    }
  }
};
</script>

